html,
body {
  font-family: "Poppins";
  background: #ff002b00;
  color: white;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE/Edge */
  user-select: none;
  height: 100%;
  margin: 0;
  scroll-behavior: smooth;
  overflow: hidden;
}
.App {
  display: grid;
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}
.homePage {
  position: relative;
}
.unicorn-studio-embed {
  width: 100%;
  height: 120vh;
  padding: 0;
  margin: 0;
  color: white;
  z-index: -10;
}
.unicorn-studio-embed a {
  opacity: 0;
}
.hideWaterMark {
  width: 100%;
  height: 100vh;
  display: flex;
  place-items: center;
  overflow: hidden;
}
.placeholder {
  background: url("./images/placeholder.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  margin: 0;
}

.emptyPage {
  width: 100%;
  height: 100vh;
  display: flex;
  place-items: center;
  justify-content: center;
}
.scroll-page {
  scroll-snap-align: start;
  height: 100vh;
}
.page2 {
  background: linear-gradient(0deg, #000000 0%, #9a0922 100%);
}
.page3 {
  background: #000000;
}
.page4 {
  background: linear-gradient(0deg, #9a0922 0%, #000000 100%);
}
.page5 {
  background: #9a0922;
}
::-webkit-scrollbar {
  display: none;
}
/* Fade-in effect for sections */
.fade-in-section {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.7s ease-out, transform 0.7s ease-out;
  will-change: opacity, transform;
}

.fade-in-section.visible {
  opacity: 1;
  transform: translateY(0px);
}
