.navbar {
  box-sizing: border-box;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  place-items: center;
  padding: 0px 40px;
  position: absolute;
}
.navbar .title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 23px;
  color: white;
  font-family: "Poppins";
  text-shadow: 0px 0px 20px #ffffff00;
  text-decoration: underline;
  text-underline-offset: 7px;
  transition: all 0.1s ease-in;
  animation: underlines 2s ease-in alternate;
}
.navbar .title:hover,
.navlinks a:hover {
  color: #ffffff;
  text-decoration: underline;
  text-underline-offset: 2px;
  text-shadow: 0px 0px 20px #ffffff;
}
@keyframes underlines {
  0% {
    transform: translateY(40px);
    text-underline-offset: 40px;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    transform: translateY(40px);
    text-underline-offset: 40px;
  }
  100% {
    transform: translateY(0px);
    text-underline-offset: 2px;
  }
}
.navlinks {
  display: flex;
  justify-content: center;
  place-items: center;
  gap: 25px;
  font-size: 23px;
  padding: 10px 20px;
  background: #9292ff1d;
  backdrop-filter: blur(10px);
  border-radius: 10px;
  z-index: 100;
}
.navlinks a {
  display: flex;
  place-items: center;
  gap: 5px;
  color: #ffffff;
  text-shadow: 0px 0px 30px #003cff;
  transition: all 0.1s ease-in;
  text-decoration: none;
  text-underline-offset: 7px;
}
.menuBar {
  width: 700px;
  height: 80px;
  background: #9292ff1d;
  backdrop-filter: blur(10px);
  box-shadow: 0px 0px 0px #ffffff00;
  position: fixed;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  border-radius: 20px 20px 0px 0px;
  transition: all 0.5s ease-in-out;
  display: flex;
  justify-content: center;
  place-items: center;
}
.menuBar:hover {
  box-shadow: 0px 0px 60px 30px #afafff;
}
.menuBar div {
  font-family: sans-serif;
  font-size: 20px;
  color: white;
}
